.appWrapper {
  width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  grid-template-areas:
  'h h'
  'n c'
}



.appWrapperContent {
  grid-area: c;
  background-color: #E1D0C0;
  border: 4px solid #FFFFFF;
  outline: 6px solid #E1D0C0;
  margin: 5px;
  font-family: 'Dancing Script', cursive;
  font-size: 20px;
  box-shadow: 0 0 10px 4px #E1D0C0;
  padding-left: 10px;
}

